import { useEffect, useState } from 'react';
import { AboutResponse } from '../../models/versionManageModel';
import { HomeService } from '../../services/homeService';
import MyTitle from '../core/Title';
import AboutItem from './AboutItem';

export default function About() {
  const [dataSource, setDataSource] = useState<AboutResponse | null>(new AboutResponse());
  function GetListLogVersion() {
    HomeService.GetListLogVersion()
      .then((rs: any) => {
        if (rs.isSuccess) {
          setDataSource(new AboutResponse(rs.data));
        }
      })
  }

  useEffect(() => {
    GetListLogVersion();
  }, []);

  return (
    <div>
      <MyTitle name="ThÃ´ng tin" />
      <AboutItem title="Changelog Frontend" data={dataSource?.frontEnd} />
      <AboutItem title="Changelog Backend" data={dataSource?.backEnd} />
      <AboutItem title="Changelog Database" data={dataSource?.database} />
    </div>
  );
}